html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

* { -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a { text-decoration: none; color: inherit; }

img { max-width: 100%; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

@font-face { font-family: "comfortaa-regular"; src: url("../fonts/comfortaa-regular-webfont.woff2") format("woff2"), url("../fonts/comfortaa-regular-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: "comfortaa-bold"; src: url("../fonts/comfortaa-bold-webfont.woff2") format("woff2"), url("../fonts/comfortaa-bold-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: "comfortaa-light"; src: url("../fonts/comfortaa-light-webfont.woff2") format("woff2"), url("../fonts/comfortaa-light-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: "clarendon-black"; src: url("../fonts/tt0351m_-webfont.woff2") format("woff2"), url("../fonts/tt0351m_-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: "pfreminderpro-medium"; src: url("../fonts/pfreminderpro-medium-webfont.woff2") format("woff2"), url("../fonts/pfreminderpro-medium-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

small { font-size: inherit; }

em, i { font-style: normal; }

h1, h2, h3, h4, h5, h6 { font-weight: 400; }

.h1, h1 { font-size: 36px; font-weight: 700; }

.h1 strong, h1 strong { font-family: 'clarendon-black', Arial, sans-serif; }

.h1 strong sup, h1 strong sup { vertical-align: super; font-size: .5em; }

.h1--purple, h1--purple { color: #341c77; }

.h2, h2 { font-size: 32px; font-weight: 700; }

.h2--accent, h2--accent { color: #fd5c37; }

body { background: #FFF; line-height: 1.4; font-size: 15px; font-family: 'Comfortaa', cursive; font-weight: 300; color: #4f4f4f; }

body.no-scroll { overflow: hidden; }

.no-touch body.no-scroll { margin-right: 17px; }

input, textarea { font-family: inherit; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder { font-family: inherit; }

input::placeholder, textarea::placeholder { font-family: inherit; }

.icon { display: inline-block; fill: currentColor; font-size: inherit; }

.container { max-width: 1440px; padding-left: 15px; padding-right: 15px; margin-left: auto; margin-right: auto; }

.phone { display: block; font-size: 17px; color: #341c77; font-weight: 700; }

.no-touch .phone:hover { text-decoration: underline; }

.phone__icon { display: inline-block; width: 15px; }

.phone__icon .icon { font-size: 14px; }

.tm { font-family: 'clarendon-black', Arial, sans-serif; }

.tm sup { font-size: .5em; vertical-align: super; }

.tm--text { color: #4f4f4f; }

.ic { display: inline-block; }

.ic-star { background-image: url("../img/sprite.png"); width: 62px; height: 48px; background-position: -276px -174px; }

.header { background-color: #fff; }

.header__inner { height: 100px; display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.header__top { box-shadow: 0px 1px 9px 0px rgba(0, 0, 0, 0.14); }

.header__logo { width: 16.66667%; }

.header__title { font-weight: 700; font-size: 24px; }

.header__btns { flex-grow: 1; display: flex; justify-content: space-around; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.header__nav { flex-grow: 1; }

.header__controls { width: 16.66667%; display: flex; justify-content: flex-end; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.header__burger { margin-left: 10px; }

.header__cart { margin-left: 40px; }

.cart-btn { color: #808080; position: relative; display: inline-block; transition: color 0.3s; }

.no-touch .cart-btn:hover { color: #341c77; }

.cart-btn .icon { font-size: 21px; }

.cart-btn sup { display: inline-block; vertical-align: top; font-size: 10px; color: #4f4f4f; background-color: #ffcc53; border-radius: 50%; height: 16px; width: 16px; line-height: 16px; text-align: center; position: relative; top: -.8em; left: -.5em; }

.burger { color: #808080; transition: color 0.3s; }

.no-touch .burger:hover { color: #341c77; }

.burger .icon { font-size: 21px; }

.lang { font-size: 17px; color: #808080; position: relative; padding-right: 15px; }

.lang::before { content: ''; width: 10px; height: 10px; border-left: 1px solid currentColor; border-bottom: 1px solid currentColor; transform: rotate(-45deg); position: absolute; right: 0; top: .3em; pointer-events: none; }

.no-touch .lang:hover .lang__list { visibility: visible; opacity: 1; }

.lang__list { position: absolute; width: 100%; left: 0; top: 100%; z-index: 1; visibility: hidden; opacity: 0; transition: opacity 0.3s, visibility 0.3s; }

.btn { display: inline-block; text-align: center; background-color: #fd5c37; border: 2px solid #fd5c37; border-radius: 35px; padding: 17px 38px; font-weight: 700; color: #fff; transition: color 0.3s, background-color 0.3s, border-color 0.3s; }

.btn--transp-purple { background-color: transparent; color: #341c77; border-color: #341c77; }

.no-touch .btn--transp-purple:hover { background-color: #341c77; border-color: #341c77; color: #fff; }

.btn-link { font-size: 17px; font-weight: 400; color: #341c77; display: inline-flex; justify-content: flex-start; align-items: center; }

.no-touch .btn-link:hover { text-decoration: underline; }

.btn-link .icon { fill: #fd5c37; margin-right: 10px; }

.nav ul { display: flex; justify-content: space-around; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.nav li { display: inline-block; }

.nav a { display: block; position: relative; color: #808080; transition: color 0.3s; padding-top: 30px; padding-bottom: 30px; font-weight: 700; }

.no-touch .nav a:hover { color: #341c77; }

.no-touch .nav a:hover .ic { visibility: visible; opacity: 1; }

.nav a .ic { visibility: hidden; opacity: 0; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); z-index: 0; transition: visibility 0.3s, opacity 0.3s; }

.nav a span { position: relative; z-index: 1; }

.hero { background-color: #f5f7fa; text-align: center; padding-top: 100px; padding-bottom: 30px; }

.hero__top { display: flex; justify-content: flex-start; align-items: normal; flex-direction: row; flex-wrap: nowrap; position: relative; }

.hero__top::before { content: ''; position: absolute; background-color: #b5b5b5; height: 1px; width: 40px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

.hero__title { width: 50%; text-align: center; padding-left: 50px; padding-right: 50px; }

.hero__descr { width: 50%; padding-left: 75px; margin-bottom: 30px; text-align: left; }

.hero__title-bottom { margin-bottom: 25px; }

.hero-descr__title { color: #ffcc53; font-size: 45px; font-weight: 700; line-height: 1.1; margin-bottom: 12px; }

.hero-descr__title small { font-size: 15px; font-weight: 300; }

.hero-descr__title span { font-size: 15px; color: #4f4f4f; display: block; font-weight: 300; }

.arrow-down { color: #fd5c37; position: relative; display: inline-block; width: 45px; height: 45px; }

.arrow-down span { position: absolute; width: 10px; transform: translate(-50%, 0); left: 50%; top: 0; height: 45px; transition: height 0.3s; }

.arrow-down span::before { content: ''; position: absolute; height: 100%; width: 1px; background-color: currentColor; left: 50%; top: 0; transform: translate(-50%, 0); }

.arrow-down span::after { content: ''; width: 10px; height: 10px; border-left: 1px solid currentColor; border-bottom: 1px solid currentColor; transform: rotate(-45deg); position: absolute; left: 50%; bottom: 0; transform: translate(-50%, 0) rotate(-45deg); }

.item-links { display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: nowrap; max-width: 1645px; padding-left: 15px; padding-right: 15px; margin-left: auto; margin-right: auto; }

.item-link { display: block; }

.item-link__img { background-color: #fff; box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09); border-radius: 22px; margin-bottom: 20px; display: block; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-size: contain; padding-top: 80%; position: relative; }

.item-link__title { color: #8cc4ff; font-weight: 700; font-size: 17px; position: absolute; bottom: 15px; left: 50%; transform: translate(-50%, 0); }
