.btn {
  display: inline-block;
  text-align: center;
  background-color: $accent;
  border: 2px solid  $accent;
  border-radius: 35px;
  padding: 17px 38px;
  @include bold;
  color: $white;
  @include tr(color .3s, background-color .3s, border-color .3s);

  &--transp-purple {
    background-color: transparent;
    color: $purple;
    border-color: $purple;
    @include hover {
      background-color: $purple;
      border-color: $purple;
      color: $white; } } }

.btn-link {
  font-size: $fz-md;
  @include reg;
  color: $purple;
  @include inline-flex(flex-start, center);
  @include hover {
    text-decoration: underline; }
  .icon {
    fill: $accent;
    margin-right: 10px; } }
