.item-links {
  @include flex(space-between, center);
  max-width: 1645px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.item-link {
  display: block;
  &__img {
    background-color: $white;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
    border-radius: 22px;
    margin-bottom: 20px;
    display: block;
    @include cover;
    background-size: contain;
    padding-top: 80%;
    position: relative; }
  &__title {
    color: $blue;
    font-weight: 700;
    font-size: $fz-md;
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%, 0); } }
