.nav {
  ul {
    @include flex(space-around, center); }
  li {
    display: inline-block; }

  a {
    display: block;
    position: relative;
    color: $gray;
    @include tr(color .3s);
    padding-top: 30px;
    padding-bottom: 30px;
    @include bold;
    @include hover {
      color: $purple;
      .ic {
        visibility: visible;
        opacity: 1; } }
    .ic {
      visibility: hidden;
      opacity: 0;
      @include vcenter;
      z-index: 0;
      @include tr(visibility .3s, opacity .3s); }
    span {
      position: relative;
      z-index: 1; } } }
